  border: none;
  outline: none;
  transition: all .3s ease-in-out;
}

input:hover, input:active, input:focus {
  border: none;
  outline: none;
}

button {
  cursor: pointer;
  outline: none;
}

button:active {
  outline: none;
}

button:focus {
  outline: none;
}

img {
  max-width: 100%;
  height: auto;
}

select {
  outline: none;
}

select:focus {
  outline: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
  border: none;
  cursor: pointer;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus
input:-webkit-autofill,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: 0;
  -webkit-text-fill-color: #сссccc;
  -webkit-box-shadow: 0 0 0px 1000px transparent inset;
  transition: background-color 5000s ease-in-out 0s;
  background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 0%, rgba(0, 174, 255, 0.04) 50%, rgba(255, 255, 255, 0) 51%, rgba(0, 174, 255, 0.03) 100%);
}

.g-app {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow-x: hidden;
}

.g-content {
  flex: 1 0 auto;
}

.g-container {
  width: 100%;
  padding: 0 15px;
  margin: auto;
  max-width: 1060px;
}

@media (max-width: 1060px) {
  .g-container {
    max-width: 100%;
  }
}

.g-footer {
  flex-shrink: 0;
}

.g-subtitle {
  font-weight: bold;
  font-size: 18px;
  text-align: center;
  color: #000000;
}

.g-text {
  font-size: 14px;
  color: #000000;
}

.g-description {
  font-size: 16px;
  color: #000000;
}

.header {
  min-height: 500px;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.header-wrapper {
  position: relative;
  min-height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px 0;
}

.header-wrapper__link {
  font-weight: normal;
  font-size: 16px;
  color: #FFFFFF;
  margin-left: 30px;
}

.header-wrapper__link:hover {
  color: #FFFF00;
}

.header-wrapper__link:active {
  color: #FF0000;
}

.header-wrapper__box {
  display: flex;
}

.header-wrapper__nav {
  display: flex;
}

.nav-list {
  display: flex;
}

.nav-list__item {
  margin-left: 60px;
}

.nav-list__link {
  font-weight: normal;
  font-size: 16px;
  color: #FFFFFF;
}

.nav-list__link:hover {
  color: #FFFF00;
}

.nav-list__link:active {
  color: #FF0000;
}

.header-content {
  min-height: 400px;
  max-width: 880px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.header-content__title {
  font-family: "Open Sans";
  font-weight: 300;
  font-size: 48px;
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 84px;
}

.header-content__desc {
  font-size: 18px;
  color: #FFFFFF;
  text-align: center;
}

.burger {
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  position: absolute;
  top: 24px;
  right: 15px;
  background: transparent;
  width: 60px;
  cursor: pointer;
  z-index: 3;
  display: none;
}

.burger span {
  display: block;
  width: 60%;
  height: 1px;
  margin-bottom: 6px;
  background: #FFFFFF;
  transition: all .3s ease;
}

.burger span:first-child {
  width: 70%;
}

.burger span:last-child {
  width: 45%;
}

.burger:hover span {
  width: 65%;
  background: #FFFF00;
}

.burger.open span {
  width: 70%;
  background: #19263F;
}

.burger.open span:hover {
  color: #19263F;
  background: #19263F;
}

.burger.open span:last-child {
  display: none;
}

.burger.open span:first-child {
  transform: rotate(45deg);
  margin-bottom: 0;
}

.burger.open span:nth-child(2n) {
  transform: rotate(-45deg);
}

.mob-menu {
  position: fixed;
  top: 0;
  right: -4000px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  background: #FFFFFF;
  width: 400px;
  height: 100vh;
}

.mob-menu.mob-menu--show {
  right: 0;
}

.mob-menu__item {
  margin-bottom: 15px;
}

.mob-menu__link {
  font-size: 16px;
  text-transform: uppercase;
}

.mob-menu__link:active {
  color: #FF0000;
}

.about__title {
  margin-top: 80px;
  margin-bottom: 30px;
}

.about__item {
  min-height: 323px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  transition: all .3s ease-in-out;
  cursor: pointer;
  background: #FAFAFA;
}

.about__item:hover {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.about__icon {
  margin-bottom: 25px;
}

.about__subtitle {
  margin-bottom: 11px;
}

.about__desc {
  margin-bottom: 37px;
}

.about__link {
  font-weight: normal;
  font-size: 14px;
  color: #1657AB;
}

.about__link:hover {
  color: #FFFF00;
}

.about__link:active {
  color: #FF0000;
}

.features__title {
  margin-top: 80px;
  margin-bottom: 30px;
}

.features__content {
  margin-bottom: 57px;
  font-size: 16px;
}

.features__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  margin-bottom: 45px;
}

.features__icon {
  margin-bottom: 25px;
}

.features__subtitle {
  margin-bottom: 11px;
}

.features__desc {
  text-align: center;
}

.features__link {
  min-height: 56px;
  min-width: 230px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #FFFFFF;
  border: 1px solid #85B657;
  border-radius: 4px;
  font-weight: normal;
  font-size: 14px;
  color: #000000;
}

.features__link:hover {
  color: #FFFF00;
}

.features__link:active {
  color: #FF0000;
}

.features__link-inner {
  display: inline-block;
  color: #1657AB;
}

.features__link-inner:hover {
  color: #FFFF00;
}

.features__link-inner:active {
  color: #FF0000;
}

.promo__title {
  margin-top: 80px;
  margin-bottom: 30px;
}

.promo__desc {
  font-weight: normal;
  font-size: 16px;
  line-height: 21px;
  margin-bottom: 20px;
}

.promo__item {
  outline: none;
}

.promo-slides {
  margin-top: 30px;
  margin-bottom: 30px;
}

.slick-slider .slick-track, .slick-slider .slick-list {
  display: flex;
  align-items: center;
}

.slick-slide img {
  margin: auto;
  max-width: 95px;
}

.footer {
  padding: 40px 0 50px 0;
  background: #19263F;
}

.footer__copy {
  color: #FFFFFF;
  text-align: center;
  font-weight: bold;
  font-size: 15px;
}

.footer-list {
  display: flex;
  justify-content: center;
  margin-bottom: 26px;
}

.footer-list__item {
  margin-left: 25px;
}

.footer-list__item:first-child {
  margin-left: 0;
}

.footer-list__link {
  color: #FFFFFF;
  font-size: 16px;
  text-transform: uppercase;
}

.social-list {
  display: flex;
  justify-content: center;
  margin-bottom: 28px;
}

.social-list__item {
  margin-left: 30px;
}

.social-list__item:first-child {
  margin-left: 0;
}

.social-list__link {
  font-size: 0;
  position: relative;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  border: 1px solid transparent;
}

.social-list__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 32px;
  height: 32px;
  color: #FFFFFF;
}

.social-list__link:hover {
  background: transparent;
  border: 1px solid rgba(255, 255, 255, 0.3);
}

@media (max-width: 1024px) {
  .burger {
    display: flex;
  }
  .header-wrapper__box {
    display: none;
  }
  .nav-list {
    display: none;
  }
  .header-content {
    max-width: 100%;
    padding-top: 100px;
  }
  .header-content__title {
    font-size: 38px;
    margin-bottom: 35px;
  }
  .burger {
    top: 40px;
  }
  .header-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    background: #19263F;
    z-index: 3;
    padding: 24px 15px;
  }
}

@media (max-width: 768px) {
  .mob-menu {
    width: 100%;
  }
  .header-content__title {
    font-size: 32px;
  }
  .header-wrapper__logo {
    max-width: 150px;
  }
  .about__title, .features__title, .promo__title {
    margin-top: 30px;
    margin-bottom: 30px;
  }
  .footer-list {
    flex-wrap: wrap;
  }
  .footer-list__item {
    margin-left: 0;
    margin-bottom: 20px;
    width: 100%;
    text-align: center;
  }
}
